var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"home",attrs:{"id":"app"}},[_c('div',{staticClass:"about-text"},[_c('h1',{staticStyle:{"padding-left":"4rem","padding-right":"4rem","padding-bottom":"1rem","font-weight":"bolder"}},[_vm._v("About")]),_c('p',{staticStyle:{"padding-left":"4rem","padding-right":"4rem"}},[_vm._v("From an innocent conversation on twitter to a reality, we are making the 3rd Homebrew contest for the PSVita. These past few days/months have been incredible for the PSVita. From a silent scene to an outburst of unlocked possibilities like the dreamcast emulators, GTA games on PSVita and sonic games being ported, Piglet (GLES 2.0 library) found and VitaGL being more complete than ever, there has been a sudden change in the PSVita scene for the better. This is why we have decided to call the contest KyûHEN (which means sudden change in Japanese, as well as keeping the HEN tradition like HENkaku,TaiHEN, GekiHEN). We want to help the vita scene to pursue that change by growing the interest about the homebrew scene with this contest. Calling people to create new contents for the PSVita using the existing tools like VitaGL, SDL, Unity, Piglet for games, and the vita/dolce SDK for the utility. If you want to be part of the journey and contribute with your project, then welcome to the KyûHEN contest!")])]),_c('div',{staticClass:"container p-5",staticStyle:{"text-align":"left"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 p-5"},[_c('h3',{staticStyle:{"font-weight":"bolder"}},[_vm._v("Samilop \"Cimmerian\" Iter")]),_c('p',[_vm._v(" I am one of the moderators of Custom Protocol (French hacking websites) as well as a former GekiHEN creator. I love using my knowledge to assist people navigating the Vita hacking scene in the henkaku and vita nuova discord’s and through my Vita troubleshooting guides. I frequently contribute to homebrew projects with beta testing 